import React, { useState } from 'react';

import Dropdown from '#components/Dropdown';
import Checkbox from '#components/Form/Input/Checkbox/Checkbox';
import { SIZE_SMALL as ICON_SIZE_SMALL } from '#components/Icon/Icon.constants';

import {
  CheckboxWrapper,
  ChevronIcon,
  DropdownContent,
  DropdownHeader,
  Label,
  Wrapper,
} from './CheckboxWithDropdown.styles';
import { CheckboxWithDropdownProps } from './CheckboxWithDropdown.types';

import ChevronDownSvg from '#icons/chevron-down-solid-16.svg';

const CheckboxWithDropdown: React.FC<CheckboxWithDropdownProps> = ({
  dropdownHeader,
  dropdownContent,
  label,
  ...checkboxProps
}) => {
  const [isOpen, setIsOpen] = useState(false);
  const onDropdownInteract = () => setIsOpen((isOpen) => !isOpen);

  return (
    <Wrapper>
      <CheckboxWrapper>
        <Checkbox label={<Label>{label}</Label>} {...checkboxProps} />
      </CheckboxWrapper>
      <Dropdown
        isOpen={isOpen}
        onInteract={onDropdownInteract}
        trigger={
          <DropdownHeader>
            {dropdownHeader}
            <ChevronIcon
              $isOpen={isOpen}
              svg={ChevronDownSvg}
              size={ICON_SIZE_SMALL}
            />
          </DropdownHeader>
        }
      >
        <DropdownContent>{dropdownContent}</DropdownContent>
      </Dropdown>
    </Wrapper>
  );
};

export default CheckboxWithDropdown;
